// 產品列別選單
.products-header {
  display: flex;
  justify-content: center;
  text-align: center;
  @include container;

  @include respond($lg) {
    padding-left: 3.6rem;
    padding-right: 3.6rem;
  }
  @include respond($md) {
    padding-left: 0;
    padding-right: 0;
  }

  &__item {
    @include roboto(3.2rem, 400);
    width: calc(100% / 3);
    border: 1px solid $color-grey-200;

    &:not(:last-child) {
      border-right: none;
    }

    @include respond($md) {
      @include font-size(1.6rem);
    }

    a {
      width: 100%;
      padding: 2.4rem 0;
      display: inline-block;

      @include respond($md) {
        padding: 1.6rem 1.3rem;
      }
    }

    &--active,
    &:hover  {
      border-bottom: 8px solid $color-primary;
    }
  }

  &__image {
    display: flex;
  }
}
// 下方產品列表
.products {
  &__content {
    @include content;
  }

  &__text {
    text-align: center;
    margin-bottom: 3.2rem;

    @include respond($md) {
      margin-bottom: 2.4rem;
    }
  }

  &__title {
    @include roboto(4.2rem, 500);

    @include respond($md) {
      @include font-size(2.4rem);
    }
  }

  &__caption {
    @include font-size(3.2rem);

    @include respond($md) {
      @include font-size(2.4rem);
    }
  }

  &__list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 -1.2rem;
  }

  &__item {
    padding: 0 1.2rem;
    width: 25%;
    margin-bottom: 8rem;

    @include respond($lg) {
      width: 50%;

      &:not(:last-child) {
        margin-bottom: 4rem;
      }
    }

    @include respond($md) {
      width: 100%;
      margin-bottom: 6.4rem;
    }

    &--sunglass {
      width: 33.3%;

      @include respond($lg) {
        width: 50%;
      }
      @include respond($md) {
        width: 100%;
      }
    }
  }
  &__image {
    width: 100%;
  }

  &__detail {
    display: flex;
    justify-content: space-between;
  }

  &__name {
    @include roboto(2.4rem, 400);
  }

  &__price {
    @include roboto(2.4rem, 500);
    color: $color-primary;
  }
}

.products-color {
  &__item {
    display: inline-block;
    width: 2.4rem;
    height: 2.4rem;
    border-radius: 4px;
    margin-right: 0.8rem;
  }
}

